@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{padding:0;margin:0;box-sizing:border-box}body{font-family:'Nunito', sans-serif;background:linear-gradient(to right, #F6F8FE, #F6F8FE)}.app-title{margin:1rem auto;font-size:1.5rem;color:#203B58}.container{width:100%;margin:0 auto;display:flex;justify-content:space-between}.sidebar{width:25%;height:100%;padding:1rem;background-color:white;border-bottom-right-radius:20px;border-top-right-radius:20px;min-height:100vh;box-shadow:2px 4px 40px 0px rgba(0,0,0,0.05)}.sidebar .active-question-number{margin-bottom:1rem;color:#f8176d;font-size:2.5rem}.sidebar .pagination .pagination-top{display:flex;align-items:center;justify-content:space-between}.sidebar .pagination .pagination-top button{width:80px;margin-bottom:1rem}.sidebar .pagination button{width:35px;height:35px;border-radius:15px;cursor:pointer;background-color:white;border:1px solid rgba(0,0,0,0.1);color:#203B58;margin:1px;transition:0.3s}.sidebar .pagination button.active,.sidebar .pagination button:hover{background-color:#f8176d;color:white}.main-content{width:75%;padding:2rem}.main-content .audio-player{margin:0 auto 2rem auto}.main-content .audio-player .rhap_container{box-shadow:2px 4px 40px 0px rgba(0,0,0,0.05);padding:2rem;border-radius:20px}.main-content .description{margin-bottom:1.5rem}.main-content .text-note{width:100%;height:200px;border-radius:20px;border:none;padding:2rem;box-shadow:2px 4px 40px 0px rgba(0,0,0,0.05);transition:0.3s;font-size:1rem;font-family:'Nunito', sans-serif}.main-content .text-note:focus{outline:none;box-shadow:2px 4px 40px 0px rgba(248,23,109,0.1)}.main-content .question-box{background-color:white;padding:2rem;border-radius:20px;box-shadow:2px 4px 40px 0px rgba(0,0,0,0.05)}.main-content .question-keywords{margin-top:2rem}.main-content .keywords-list{display:inline-block}.main-content .btn-check-keywords{width:200px;padding:1.2rem 2rem;border-radius:20px;border:none;color:white;background-color:#f8176d;font-size:1rem;cursor:pointer;transition:0.3s;font-weight:600;display:block;margin:1.5rem auto 2rem auto}.main-content .btn-check-keywords:hover{background-color:#ff3d88}.main-content .matched-keyword{background-color:#23ffab;padding:0 4px}.main-content .unmatched-keyword{color:#ff0a68}.main-content .question-transcripts{line-height:1.5}.main-content .question-transcripts .keyword{background-color:#23ffab;padding:0 4px}

